<template>
  <div class="web-base-custom">
    <!--头部-->
    <div class="header-wrap">
      <div class="heaader-inner-wrap flex flex-x-between flex-item-center paddingx20">
        <div class="logo-wrap" @click="$router.push('/')">
          <img src="@/assets/img/logo.png" />
        </div>
        <div class="menu-wrap">
          <svg-icon icon="menu" @click="showMenu = !showMenu" />
        </div>
      </div>

      <van-popup class="nav-popup" position="right" v-model="showMenu">
        <div class="title flex flex-x-between flex-item-center">
          <img src="@/assets/img/logo.png" />
          <svg-icon @click="showMenu = false" class="close" icon="close" />
        </div>
        <ul class="item-ul">
          <li v-for="(v, k) in nav" @click="navSwitch(v)" :class="currentNav.id == v.id ? 'active' : ''">{{ v.name }}
          </li>
        </ul>

      </van-popup>

    </div>

    <div class="content-wrap">
      <router-view />
    </div>

    <!--页脚-->
    <Footer />

  </div>
</template>
<script>
import Footer from './Footer.vue'
export default {
  components: { Footer },
  name: 'Home',
  data() {
    return {
      currentNav: { id: 1 },
      showMenu: false,
      nav: [
        { id: 1, name: '首页', path: '/h5/home' },
        { id: 2, name: '客户服务', path: '/h5/customer' },
        { id: 20, name: '成功案例', path: '/h5/success' },
        { id: 3, name: '定价', path: '/h5/price' },
        { id: 4, name: '联系我们', path: '/h5/me' },
      ]
    }
  },
  methods: {
    navSwitch(v) {
      debugger
      this.showMenu = false;
      if (this.currentNav && this.currentNav.id == v.id) return;
      this.$router.push(v.path)
      this.currentNav = v;
    },
  },
  mounted() {
    this.$router.push(this.nav[0].path)
  }
}
</script>
