import Vue from 'vue'
import VueRouter from 'vue-router'

import Home from "@/views/Home.vue"
import WebHome from "@/views/web/home/Home.vue"
import HomeContent from "@/views/web/home/HomeContent.vue"
import SuccessCase from "@/views/web/home/SuccessCase.vue"
import WebPrice from "@/views/web/home/Price.vue"
import WebMe from "@/views/web/home/Me.vue"
import H5Home from "@/views/h5/home/Home.vue"
import H5HomeContent from "@/views/h5/home/HomeContent.vue"
import H5SuccessCase from "@/views/h5/home/SuccessCase.vue"
import H5Price from "@/views/h5/home/Price.vue"
import H5Me from "@/views/h5/home/Me.vue"
import H5Customer from "@/views/h5/home/Customer.vue"
import Error from "@/views/common/404.vue"

Vue.use(VueRouter)

const routes = [
{
  path: '/',
  name: 'hm',
  component: Home,
},
{
  path: '/web',
  name: 'WebHome',
  component: WebHome,
  children: [{
    path: 'success',
    name: 'SuccessCase',
    component: SuccessCase,
  }, {
    path: 'home',
    name: 'HomeContent',
    component: HomeContent,
  }, {
    path: 'price',
    name: 'price',
    component: WebPrice,
  }, {
    path: 'me',
    name: 'me',
    component: WebMe,
  }, ]
}, {
  path: '/h5',
  name: 'H5Home',
  component: H5Home,
  children: [{
    path: 'success',
    name: 'H5SuccessCase',
    component: H5SuccessCase,
  }, {
    path: 'home',
    name: 'H5HomeContent',
    component: H5HomeContent,
  }, {
    path: 'price',
    name: 'h5price',
    component: H5Price,
  }, {
    path: 'me',
    name: 'h5me',
    component: H5Me,
  }, {
    path: 'customer',
    name: 'H5Customer',
    component: H5Customer,
  }, ]
},
{
  path: '*',
  name: 'error',
  component: Error,
}]

const router = new VueRouter({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes
})

// 路由前
router.beforeEach((to, from, next) => {
  let title = to.matched[0].meta.title + ""; // 获取到标题
  if ("index1" == title) { // 条件成立时,跳转到指定页面
    router.replace("/profile");
  } else if ("index2" == title) {
    return; // 不做任何操作,直接跳出该方法,所以是不会进行下面修改标题的代码
  }
  document.title = '江明软件'; // 改变标题
  next()
});


export default router